'use client'

import Headline from '@/components/ui/Headline'
import ProjectCard from '@/components/portfolio/ProjectCard'
import styles from './PortfolioSection.module.scss'

const BASE_PATH = process.env.NEXT_PUBLIC_BASE_PATH || ''

export default function PortfolioSection() {
  return (
    <section className={styles.container}>
      <div className={styles.header}>
        <Headline lineTop="Selected works" lineBottom="Portfolio" />
      </div>

      <div className={styles.projects}>
        <ProjectCard
          videoSrc={`${BASE_PATH}/videos/fixmobile.mp4`}
          logoSrc={`${BASE_PATH}/logos/fixmobile.svg`}
          title="FixMobile"
          description="Redesigned the brand identity, including a new logo and cohesive visuals across physical and digital touchpoints..."
        />
        <ProjectCard
          videoSrc={`${BASE_PATH}/videos/masterbean.mp4`}
          logoSrc={`${BASE_PATH}/logos/masterbean.svg`}
          title="MasterBean"
          description="Developed a complete branding package with logo and packaging design. Delivered the website concept..."
        />
        <ProjectCard
          videoSrc={`${BASE_PATH}/videos/rubenrub.mp4`}
          logoSrc={`${BASE_PATH}/logos/rubinrub.svg`}
          title="RubinRub"
          description="Created a range of branding and marketing assets, including visuals for social media and promotions..."
        />
        <ProjectCard
          videoSrc={`${BASE_PATH}/videos/stefanel.mp4`}
          logoSrc={`${BASE_PATH}/logos/stefanel.svg`}
          title="Stefanel"
          description="Handled in-store visual merchandising, planning product layouts and display strategies..."
        />
        <ProjectCard
          videoSrc={`${BASE_PATH}/videos/btx.mp4`}
          logoSrc={`${BASE_PATH}/logos/btx.svg`}
          title="BTX Outlet"
          description="Redesigned the logo and created consistent visual materials for digital and print..."
        />
        <ProjectCard
          videoSrc={`${BASE_PATH}/videos/seoquill.mp4`}
          logoSrc={`${BASE_PATH}/logos/seo-quill-logo.svg`}
          title="SeoQuill"
          description="Created the brand identity, logo, and core product concept, shaping the platform‚Äôs visual and functional direction..."
        />
      </div>
    </section>
  )
}
